<template>
  <div class="container">
    <template v-if="loading"><Loader /></template>
    <div class="row" v-else>
      <div class="col-sm-6 mx-auto">
        <h3>Edit Station</h3>
        <form @submit.prevent="handleUpdate(formStation)">
          <div class="form-group">
            <label>Name:</label>
            <input type="text" class="form-control" v-model="formStation.name" />
          </div>
          <div class="form-group">
            <label>Address:</label>
            <input type="text" class="form-control" v-model="formStation.address" />
          </div>
          <div class="form-group">
            <label>Province:</label>
            <input type="text" class="form-control" v-model="formStation.province" />
          </div>
          <button type="submit" class="btn btn-success">Update</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import Loader from './../../../components/Loader';
export default {
  components: {
    Loader
  },
  data() {
    return {
      formStation: {
        name: '',
        address: '',
        province: ''
      }
    };
  },
  created() {
    this.$store.dispatch('fetchDetailStation', this.$route.params.id);
  },
  computed: {
    loading() {
      return this.$store.state.station.loading;
    },
    stationDetails() {
      return this.$store.state.station.data;
    }
  },
  watch: {
    stationDetails(newValue) {
      this.formStation = newValue;
    }
  },
  methods: {
    handleUpdate(formStation) {
      formStation.id = this.$route.params.id;
      this.$store.dispatch('fetchUpdateStation', formStation);
    }
  }
};
</script>

